/*
 * Variables
 */

$text-color: #333;
$quiet-color: #ccc;
$loud-color: #000;
$heading-color: #111;

$link-color: #d54aaf;
$link-hover-color: saturate(#d54aaf, 20%);
$link-visited-color: purple;

$base-font-family: 'Roboto Condensed',
sans-serif;
$base-font-size: 18px;
$base-font-weight: 300;
$base-line-height: 1.6;

$fixed-font-family: 'Andale Mono',
AndaleMono,
monospace;
$fixed-font-size: 15px;
$fixed-line-height: 1.2;

$heading-font-family: 'Roboto Condensed',
sans-serif;
$heading-font-weight: 700;


/*
 * Base styles
 */

body {
    color: $text-color;
    font-size: $base-font-size;
    font-family: $base-font-family;
    font-weight: $base-font-weight;
    line-height: $base-line-height;
}


/*
 * Typography
 */

.link {
    color: $link-color;
    text-decoration: none;

    &:visited {
        color: $link-visited-color;
    }

    &:hover,
    &:visited:hover {
        color: $link-hover-color;
        text-decoration: underline;
    }
}

.fixed {
    font-family: $fixed-font-family;
    font-size: $fixed-font-size;
    line-height: $fixed-line-height;
}

.quiet {
    font-color: $quiet-color;
}

.loud {
    font-color: $loud-color;
}

.italic {
    font-style: italic;
}

.bold {
    font-weight: 700;
    @extend .loud;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-family: $heading-font-family;
    font-weight: $heading-font-weight;
}

.h1,
.h2 {
    line-height: 1.1;
}

.h3,
.h4 {
    line-height: 1.3;
}

.h1 {
    font-size: 400%;
    letter-spacing: -2px;
}

.h2 {
    font-size: 250%;
    letter-spacing: -1px;
}

.h3 {
    font-size: 200%;
}

.h4 {
    font-size: 180%;
}

.h5 {
    font-size: 130%;
}

.h6 {
    font-size: 100%;
}

.block-margins {
    margin: 1em 0;
}

.unordered-list {
    list-style-type: disc;
    padding-left: 30px;
}

.ordered-list {
    list-style: decimal;
    padding-left: 30px;
}

.typography {
    a {
        @extend .link;
    }

    i,
    em {
        @extend .italic;
    }

    b,
    strong {
        @extend .bold;
    }

    h1,
    .h1 {
        @extend .h1;
        margin: 1em 0 0.5em;
    }

    h2,
    .h2 {
        @extend .h2;
        margin: 1em 0 0.5em;
    }

    h3,
    .h3 {
        @extend .h3;
        margin: 1em 0 0.5em;
    }

    h4,
    .h4 {
        @extend .h4;
        margin: 1em 0 0.5em;
    }

    h5,
    .h5 {
        @extend .h5;
        margin: 1em 0 0.5em;
    }

    h6,
    .h6 {
        @extend .h6;
        margin: 1em 0 0.5em;
    }

    p,
    ul,
    ol,
    pre {
        @extend .block-margins;
    }

    ul {
        @extend .unordered-list;
    }

    ol {
        @extend .ordered-list;
    }

    pre,
    code {
        @extend .fixed;
    }
}


/*
 * Modifiers
 */

.float-left {
    float: left;
    margin-right: 1em;
}

.float-right {
    float: right;
    margin-left: 1em;
}

.text-left {
    text-align: left !important;
}

.text-center {
    text-align: center !important;
}

.text-right {
    text-align: right !important;
}
